<template>
    <div v-show="!isSingleMode" class="pluginsStatusContent p-r">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="systemSetup">
                <span slot="label">
                    <i class="fa fa-wrench m-r-5" aria-hidden="true"></i>
                    {{$t('remoteControl.systemSetup')}}
                </span>
                <el-col :span="20" class="m-t-20">
                    <el-row>
                        <el-col :md="8" :sm="12" class="pd-5">
                            <div class="info-box">
                                <span class="info-box-icon bg-DarkRed">
                                    <i class="fa fa-undo info-box-icon-white"></i>
                                    <p class="info-box-icon-title">{{$t('remoteControl.reboot')}}</p>
                                </span>
                                <el-button circle type="danger" size="small" class="fa fa-undo" aria-hidden="true" @click="batchSetup({reboot:true}, 'reboot')"></el-button>
                            </div>
                        </el-col>

                        <el-col :md="8" :sm="12" class="pd-5">
                            <div class="info-box">
                                <span class="info-box-icon bg-DarkViolet">
                                    <i class="fa fa-power-off info-box-icon-white"></i>
                                    <p class="info-box-icon-title">{{$t('remoteControl.shutdown')}}</p>
                                </span>
                                <el-button circle type="danger" size="small" class="fa fa-power-off" aria-hidden="true" @click="batchSetup({shutdown:true}, 'shutdown')"></el-button>
                            </div>
                        </el-col>

                        <el-col :md="8" :sm="12"  class="pd-5">
                            <div class="info-box">
                                <span class="info-box-icon bg-green">
                                    <i class="fa fa-volume-up info-box-icon-white"></i>
                                    <p class="info-box-icon-title">{{$t('remoteControl.audio')}}</p>
                                </span>
                                <el-slider
                                v-model="audio" 
                                @change="batchSetup({set_audio: audio}, 'audio')" 
                                :min= "0"
                                :max= "100"
                                :step= "1"
                                class="counter"
                                :format-tooltip="handleAudio"
                                >
                                </el-slider>
                            </div>
                        </el-col>

                        <el-col :md="8" :sm="12"  class="pd-5">
                            <div class="info-box">
                                <span class="info-box-icon bg-Indigo">
                                    <i class="fa fa-sun-o info-box-icon-white"></i>
                                    <p class="info-box-icon-title">{{$t('remoteControl.brightness')}}</p>
                                </span>
                                <el-slider 
                                v-model="brightness"
                                @change="batchSetup({set_brightness: brightness}, 'brightness')" 
                                :min= "0"
                                :max= "100"
                                :step= "1"
                                class="counter"
                                :format-tooltip="handleBrightness"
                                >
                                </el-slider>
                            </div>
                        </el-col>
                    </el-row>
                </el-col>
            </el-tab-pane>
            <el-tab-pane name="schedule">
                <span slot="label"><i class="fa fa-calendar m-r-5" aria-hidden="true"></i>
                    {{$t('remoteControl.schedule')}}
                </span>
                <el-col :md="8" :sm="12" class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-DarkRed">
                            <i class="fa fa-undo info-box-icon-white"></i>
                            <p class="info-box-icon-title">{{$t('remoteControl.reboot')}}</p>
                        </span>
                        <div class="date">
                            <el-radio v-model="radio" label="1">{{$t('remoteControl.once')}}</el-radio>
                            <el-radio v-model="radio" label="2">{{$t('remoteControl.repeat')}}</el-radio><br>
                            <el-date-picker
                            class="m-t-15"
                            v-model="rebootDate"
                            type="datetime"
                            :placeholder="$t('remoteControl.datetimeHolder')"
                            >
                            </el-date-picker>
                            <el-select
                            class="m-t-15"
                            v-if="radio==2"
                            v-model="cycle"
                            :placeholder="$t('remoteControl.cycleHolder')"
                            >
                                <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                                >
                                </el-option>
                            </el-select><br>
                            <el-button class="m-t-15" type="primary" size="small" @click="batchScheduleReboot({reboot:false}, 'reboot')">{{$t('remoteControl.disable')}}</el-button>
                            <el-button class="m-t-15" type="primary" size="small" @click="batchScheduleReboot({reboot: true}, 'reboot')">{{$t('remoteControl.save')}}</el-button>
                        </div>
                    </div>
                </el-col>
                <el-col :md="8" :sm="12" class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-DarkViolet">
                            <i class="fa fa-power-off info-box-icon-white"></i>
                            <p class="info-box-icon-title">{{$t('remoteControl.shutdown')}}</p>
                        </span>
                        <div class="date">
                            <el-date-picker
                            class="m-t-30"
                            v-model="shutdownDate"
                            type="datetime"
                            :placeholder="$t('remoteControl.datetimeHolder')"
                            >
                            </el-date-picker><br>
                            <el-button class="m-t-15" type="primary" size="small" @click="batchScheduleShutdown({shutdown:false}, 'shutdown')">{{$t('remoteControl.disable')}}</el-button>
                            <el-button class="m-t-15" type="primary" size="small" @click="batchScheduleShutdown({shutdown: true}, 'shutdown')">{{$t('remoteControl.save')}}</el-button>
                        </div>
                    </div>
                </el-col>
            </el-tab-pane>
            <el-tab-pane name="batchSetupHistory">
                <span slot="label"><i class="fa fa-history m-r-5" aria-hidden="true"></i>
                    {{$t('remoteControl.history')}}
                </span>
                <plugins-history
                ref="batchSetupHistory"
                :groupname="groupname"
                @isFinished="isFinished"
                @getStatusCount="getBatchPluginsStatusCount"
                >
                </plugins-history>
            </el-tab-pane>
        </el-tabs>
        <el-dialog :visible.sync="dialogTasknameVisible" @close="initTasknameInfo()" :close-on-click-modal="false">
            <div slot="title" class="dialog-title">
                <i class="fa fa-level-up m-r-10" ></i>
                {{$t('remoteControl.batchSystemSetup')}}
            </div>
            <el-form :model="batchSetupForm" :rules="rules" ref="batchSetupForm">
                <el-form-item :label="$t('label.taskName')" label-width="230px" prop="taskname">
                    <el-input v-model.trim="batchSetupForm.taskname" autocomplete="off" class="max-w-350" :placeholder="$t('remoteControl.batchSetupTaskName')"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogTasknameVisible = false">{{$t('global.cancel')}}</el-button>
                <el-button type="primary" @click="systemSetupAction()">{{$t('global.confirm')}}</el-button>
            </div>
        </el-dialog>
        <div class="pluginsStatusCount" v-show="isShowOverview">
            <span>{{$t('label.taskName')}} {{rm_taskname}}</span>
            <span>{{$t('appControl.total')}} {{total}}</span>
            <span>{{$t('appControl.deploying')}} {{installing}}</span>
            <span>{{$t('appControl.success')}} {{success}}</span>
            <span>{{$t('appControl.failed')}} {{failed}}</span>
        </div>
    </div>
    
</template>


<script>
    import batchSetupHistory from "./history"
    import {getDeviceStatus, 
            setDeviceStatus, 
            batchSetDeviceStatus,
            batchScheduleSetDeviceStatus,
            execDeviceStatus, 
            getPluginsStatusOverviewApi,
            } from '../../restfulapi/pluginsApi'
    import selectDevice from '../../../common/select-device'
    import {androidControl, actionDevice} from '@/assets/js/lwm2mMap'
    import handleResponse from '../../restfulapi/handleResponse'
    import selectGroup from "../../../common/select-group"
    import {pluginname} from "../../../assets/js/constant"
    import { number } from 'echarts/lib/export'
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"
    import btnGroup from "../../../common/btn-group";
    import {singleInstallType, batchInstallType} from "../../../assets/js/constant"
    import pluginsHistory from "./history"

    export default{
        name: 'batchRemoteControl',
        data(){
            let validateTaskname = (rule,value,callback)=>{
                let reg=new RegExp(/^(?![._-\s])[\u4E00-\u9FA5A-Za-z0-9_\-\.\s]{1,12}$/);
                if(!reg.test(value)){
                    return callback(new Error (this.$t('global.verifyShortError')))
                }else{callback()};
            };
            return {
                activeName: "systemSetup",
                audio: 0,
                brightness: 0,
                radio: "1",
                rebootDate: "",
                shutdownDate: "",
                cycle: "",
                dialogTasknameVisible: false,
                batchSetupForm:{
                    taskname: '',
                    actions:"",
                    datetime: "",
                    cycle: "",
                },
                rules:{
                    taskname: {required: true, validator: validateTaskname, trigger: 'blur'}
                },
                batchInstallType: batchInstallType,
                listLoading: false,
                isShowOverview: false,
                rm_taskname: '',
                success: 1,
                installing: 1,
                failed: 1,
            }
        },

        props: {
            selectedAgentsData: {
                type: Array,
                default: []
            },
            groupname: {
                type: String,
                default: ""
            }, 
            isSingleMode: {
                type: Boolean,
                default: true
            }
        },

        components: {
            pluginsHistory,
        },

        methods:{
            handleBrightness(e){
                return e + '%';
            },
            handleAudio(e){
                return e + '%';
            },
            getBatchPluginsStatusCount(){
                let rm_taskname = getLocal("rm_taskname");
                let type = "ssb"
                if(!rm_taskname){
                    console.error("getBatchPluginsStatusCount:localStorage(rm_taskname) is null")
                    this.isShowOverview = false;
                    return;
                }
                getPluginsStatusOverviewApi(rm_taskname, type).then((data) => {
                    handleResponse((data), (res) => {
                        if(res){
                            this.rm_taskname = rm_taskname;

                            this.installing = res.installing;
                            this.failed = res.fail;
                            this.success = res.success;
                            if(this.total > 0 &&  this.activeName === "batchSetupHistory"){
                                this.isShowOverview = true;
                            }else{
                                this.isShowOverview = false;
                            }
                        }
                    })
                })
            },
            getSensorStatus(){
                if(this.isSingleMode) {
                    console.error("Cannot operate in a single mode");
                    return;
                }
                if(this.selectedAgentsData.length == 0){
                    console.error("getSensorStatus: agentData is null");
                    return;
                }
            },
            isFinished(){
                this.getSensorStatus();  
            },

            initTasknameInfo(){
                this.batchSetupForm.taskname = "";
                this.batchSetupForm.actions = "";
                this.batchSetupForm.datetime = "";
                this.batchSetupForm.cycle = "";
                this.getDeviceBrightness();
                this.getDeviceAudio();
            },
            batchSetup(actions, cid){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("",this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                this.batchSetupForm.actions = actions;
                this.dialogTasknameVisible = true;
            },

            batchScheduleReboot(actions, cid){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("",this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                if(!this.rebootDate){
                    this.$swal("",this.$t('global.selectDate'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                } else {
                    this.batchSetupForm.datetime = this.rebootDate;
                }
                if(this.radio==2 && !this.cycle){
                    this.$swal("",this.$t('global.selectCycle'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                this.batchSetupForm.actions = actions;
                this.batchSetupForm.cycle = this.cycle;
                this.dialogTasknameVisible = true;
            },

            batchScheduleShutdown(actions, cid){
                if(this.selectedAgentsData.length === 0){
                    this.$swal("",this.$t('global.devicesOffline'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                if(!this.shutdownDate){
                    this.$swal("",this.$t('global.selectDate'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                } else {
                    this.batchSetupForm.datetime = this.shutdownDate;
                }
                this.batchSetupForm.actions = actions;
                this.dialogTasknameVisible = true;
            },

            systemSetupAction(){
                this.$refs.batchSetupForm.validate((valid) => {
                    if(valid) {
                        let rm_taskname = this.batchSetupForm.taskname;
                        if(this.activeName === "systemSetup"){
                            let param={
                                pluginname: pluginname,
                                actions: this.batchSetupForm.actions,
                                taskname: this.batchSetupForm.taskname,
                            }
                            this.dialogTasknameVisible = false;  
                            batchSetDeviceStatus(this.groupname, param).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status === "CHANGED"){
                                        setLocal("rm_taskname", rm_taskname);
                                        this.activeName = "batchSetupHistory";
                                        this.$refs.batchSetupHistory.intervalGetPluginsStatusHistory();
                                    }else{
                                        _g.handleError(res);
                                        this.dialogTasknameVisible =false;
                                    }
                                })
                                
                            })      
                        } else {
                            let time = new Date(this.batchSetupForm.datetime);
                            let param={
                                pluginname: pluginname,
                                actions: this.batchSetupForm.actions,
                                time: time.getTime(),
                                cycle: this.batchSetupForm.cycle,
                                taskname: this.batchSetupForm.taskname,
                            }
                            this.dialogTasknameVisible = false;  
                            batchScheduleSetDeviceStatus(this.groupname, param).then((data) => {
                                handleResponse(data, (res) => {
                                    if(res.status === "CHANGED"){
                                        setLocal("rm_taskname", rm_taskname);
                                        this.activeName = "batchSetupHistory";
                                        this.$refs.batchSetupHistory.intervalGetPluginsStatusHistory();
                                        if(param.actions.reboot===false){
                                            this.getRebootPlan();
                                        }else if(param.actions.shutdown===false){
                                            this.getShutdownPlan();
                                        }
                                    }else{
                                        _g.handleError(res);
                                        this.dialogTasknameVisible =false;
                                    }
                                })
                                
                            })
                        } 
                    }
                })
            },

            getShutdownPlan(){
                let param="{'get_shutdown_plan':true}";
                getDeviceStatus(this.selectedAgentsData[0].value, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let time = parseInt(JSON.parse(res.content.value).shutdownTime);
                            if(time){
                                this.shutdownDate = time;
                            }else{
                                this.shutdownDate = "";
                            }
                        }
                    })
                })   
            },

            getRebootPlan(){
                let param="{'get_reboot_plan':true}";
                getDeviceStatus(this.selectedAgentsData[0].value, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let time = parseInt(JSON.parse(res.content.value).rebootTime);
                            let cycle = parseInt(JSON.parse(res.content.value).rebootCycle);
                            if(this.radio ==1){
                                if(time && !cycle){
                                    this.rebootDate = time;
                                }else {
                                    this.rebootDate = "";
                                }
                            }else{
                                if(time && cycle){
                                    this.cycle = cycle;
                                    this.rebootDate = time;
                                }else{
                                    this.cycle = "";
                                    this.rebootDate = "";
                                }
                            }
                        }
                    })
                }) 
            },

            getDeviceBrightness(){
            let param="{'get_brightness': 0}";
                getDeviceStatus((this.selectedAgentsData[0]).value, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            this.brightness=parseInt(JSON.parse(res.content.value).brightness);
                        }else{
                            _g.handleError(res)
                        }
                    })
                })      
            },

            getDeviceAudio(){
                let param="{'get_audio': true}";
                getDeviceStatus((this.selectedAgentsData[0]).value, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            this.audio=parseInt(JSON.parse(res.content.value).get_audio);
                        }else{
                            _g.handleError(res)
                        }
                    })
                })      
            },

            getRebootType(){
                let param="{'get_reboot_plan':true}";
                getDeviceStatus(this.selectedAgentsData[0].value, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let cycle = parseInt(JSON.parse(res.content.value).rebootCycle); 
                            if(cycle){
                                this.cycle = cycle;
                                this.radio = "2";
                            }else {
                                this.cycle = "";
                                this.radio = "1";
                            }
                            
                        }
                    })
                }) 
            },

            handleClick(tab){
                if(tab.name === "batchSetupHistory"){
                    this.getBatchPluginsStatusCount();
                }else{
                    this.isShowOverview = false;
                }
            },
            
            initData(){
                this.rebootDate="";
                this.cycle="";
            },
            initShutdownData(){
                this.shutdownDate = "";
                this.audio = 0;
                this.brightness = 0;

            }
        },
        watch: {    
            isSingleMode(val){
                if(!val){
                    this.activeName = "systemSetup";
                    this.initData();
                    this.initShutdownData();
                    if(this.selectedAgentsData.length!=0){
                        this.getShutdownPlan();
                        this.getRebootPlan();
                        this.getRebootType();
                        this.getDeviceBrightness();
                        this.getDeviceAudio();
                    }
                }
            },
            selectedAgentsData:function(){
                this.initData();
                this.initShutdownData();
                if(this.selectedAgentsData.length!=0){
                    this.getShutdownPlan();
                    this.getRebootPlan();
                    this.getRebootType();
                    this.getDeviceBrightness();
                    this.getDeviceAudio();
                }
            },
            groupname(val){
                if(!this.isSingleMode&& val){
                    this.getSensorStatus();
                }
            },
            radio:function(){
                this.initData();
                if(this.selectedAgentsData.length!=0){
                    this.getRebootPlan();
                }
            }
        },
        computed: {
            total(){
                return  this.success+ this.installing+ this.failed;
            },

            options(){
                return [{
                    value: 1,
                    label: this.$t('remoteControl.oneDay')
                }, {
                    value: 7,
                    label: this.$t('remoteControl.oneWeek')
                }, {
                    value: 30,
                    label: this.$t('remoteControl.oneMonth')
                }, {
                    value: 90,
                    label: this.$t('remoteControl.threeMonths')
                }, {
                    value: 180,
                    label: this.$t('remoteControl.sixMonths')
                }, {
                    value: 365,
                    label: this.$t('remoteControl.oneYear')
                }]
            } 
        }       
    }
</script>

<style lang='scss' scoped>
@import "@/assets/css/colors.scss";
.pd-5{
    padding:5px
}
.counter{
    display: inline-block;
    width: 150px;
}
.info-box {
    display: block;
    min-height: 180px;
    background: #fff;
    width: 100%;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    margin: 15px 0px 15px 0px;;
    text-align: center;
  
}
.info-box-icon {
    border-radius: 50%;
    display: block;
    /* float: left; */
    height: 120px;
    width: 120px;
    
    line-height: 40px;
    font-size: 45px;
    /* background: rgba(0, 0, 0, 0.2); */
    margin-top: 15px;
    margin:1rem auto;
    i{
        margin-top: 20px;
    }
}
.info-box-icon-white {
    color: #fff;
}
.info-box-icon-title{
	color:#fff;
    font-size:15px;
    margin-bottom: 20px;
}
.div{
    margin-top: 50px;
    .p{
        margin-top: 20px; 
    }
    .i{
        margin-top: 20px;
        padding-right: 30px;
        width: 150; 
    }
    .span{
        display: block;
        float: left;
    }
}
.date{
    min-height: 130px;
}
.pluginsStatusContent{
        position: relative;
        .pluginsStatusCount{
            position: absolute;
            top: 10px;
            right: 6px;
            span{
                font-weight: 500;
                font-size: 11px;
                &:nth-child(1){
                    color: $primary-color;
                }
                &:nth-child(2){
                    border-left: 2px solid #ddd;
                    padding-left: 3px; 
                    color: $info-color;
                }
                &:nth-child(3){
                    color: $warn-color;
                }
                &:nth-child(4){
                    color: $success-color;
                }
                &:nth-child(5){
                    color: $darkgray-color;
                }
            }   
        }
    }
</style>



