<template>
    <div v-show="isSingleMode">
        <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane name="systemSetup">
                <span slot="label">
                    <i class="fa fa-wrench m-r-5" aria-hidden="true"></i>
                    {{$t('remoteControl.systemSetup')}}
                </span>
                <el-col :span="20" class="m-t-20">
                    <el-row>
                        <el-col :md="8" :sm="12" class="pd-5">
                            <div class="info-box">
                                <span class="info-box-icon bg-DarkRed">
                                    <i class="fa fa-undo info-box-icon-white"></i>
                                    <p class="info-box-icon-title">{{$t('remoteControl.reboot')}}</p>
                                </span>
                                <el-button circle type="danger" size="small" class="fa fa-undo" aria-hidden="true" @click="systemSetupAction({reboot:true}, 'reboot')"></el-button>
                            </div>
                        </el-col>

                        <el-col :md="8" :sm="12" class="pd-5">
                            <div class="info-box">
                                <span class="info-box-icon bg-DarkViolet">
                                    <i class="fa fa-power-off info-box-icon-white"></i>
                                    <p class="info-box-icon-title">{{$t('remoteControl.shutdown')}}</p>
                                </span>
                                <el-button circle type="danger" size="small" class="fa fa-power-off" aria-hidden="true" @click="systemSetupAction({shutdown:true}, 'shutdown')"></el-button>
                            </div>
                        </el-col>

                        <el-col :md="8" :sm="12"  class="pd-5">
                            <div class="info-box">
                                <span class="info-box-icon bg-green">
                                    <i class="fa fa-volume-up info-box-icon-white"></i>
                                    <p class="info-box-icon-title">{{$t('remoteControl.audio')}}</p>
                                </span>
                                <el-slider
                                v-model="audio" 
                                @change="switchChange({set_audio: audio}, 'audio')" 
                                :step="1"
                                :min="0"
                                :max="100"
                                class="counter"
                                :format-tooltip="handleAudio"
                                >
                                </el-slider>
                            </div>
                        </el-col>

                        <el-col :md="8" :sm="12"  class="pd-5">
                            <div class="info-box">
                                <span class="info-box-icon bg-Indigo">
                                    <i class="fa fa-sun-o info-box-icon-white"></i>
                                    <p class="info-box-icon-title">{{$t('remoteControl.brightness')}}</p>
                                </span>
                                <el-slider 
                                v-model="brightness"
                                @change="switchChange({set_brightness: brightness}, 'brightness')" 
                                :step= "1"
                                :min="0"
                                :max="100"
                                class="counter"
                                :format-tooltip="handleBrightness"
                                >
                                </el-slider>
                            </div>
                        </el-col>
                    </el-row>
                </el-col>
            </el-tab-pane>
            <el-tab-pane name="schedule">
                <span slot="label"><i class="fa fa-calendar m-r-5" aria-hidden="true"></i>
                    {{$t('remoteControl.schedule')}}
                </span>
                <el-col :md="8" :sm="12" class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-DarkRed">
                            <i class="fa fa-undo info-box-icon-white"></i>
                            <p class="info-box-icon-title">{{$t('remoteControl.reboot')}}</p>
                        </span>
                        <div class="date">
                            <el-radio v-model="radio" label="1">{{$t('remoteControl.once')}}</el-radio>
                            <el-radio v-model="radio" label="2">{{$t('remoteControl.repeat')}}</el-radio><br>
                            <el-date-picker
                            class="m-t-15"
                            v-model="rebootDate"
                            type="datetime"
                            :placeholder="$t('remoteControl.datetimeHolder')"
                            >
                            </el-date-picker>
                            <el-select
                            class="m-t-15"
                            v-if="radio==2"
                            v-model="cycle"
                            :placeholder="$t('remoteControl.cycleHolder')"
                            >
                                <el-option
                                v-for="item in options"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value"
                                >
                                </el-option>
                            </el-select><br>
                            <el-button class="m-t-15" type="primary" size="small" @click="actionReboot({reboot:false}, 'disable')">{{$t('remoteControl.disable')}}</el-button>
                            <el-button class="m-t-15 m-l-15" type="primary" size="small" @click="actionReboot({reboot:true}, 'reboot')">{{$t('remoteControl.save')}}</el-button>
                        </div>
                    </div>
                </el-col>
                <el-col :md="8" :sm="12" class="pd-5">
                    <div class="info-box">
                        <span class="info-box-icon bg-DarkViolet">
                            <i class="fa fa-power-off info-box-icon-white"></i>
                            <p class="info-box-icon-title">{{$t('remoteControl.shutdown')}}</p>
                        </span>
                        <div class="date">
                            <el-date-picker
                            class="m-t-30"
                            v-model="shutdownDate"
                            type="datetime"
                            :placeholder="$t('remoteControl.datetimeHolder')"
                            >
                            </el-date-picker><br>
                            <el-button class="m-t-15" type="primary" size="small" @click="actionShutdown({shutdown:false}, 'disable')">{{$t('remoteControl.disable')}}</el-button>
                            <el-button class="m-t-15 m-l-15" type="primary" size="small" @click="actionShutdown({shutdown:true},'shutdown')">{{$t('remoteControl.save')}}</el-button>
                        </div>
                    </div>
                </el-col>
            </el-tab-pane>
        </el-tabs>
    </div>
    
</template>

<script>
    import appHistory from "./history"
    import {getDeviceStatus, setDeviceStatus, execDeviceStatus} from '../../restfulapi/pluginsApi'
    import selectDevice from '../../../common/select-device'
    import {androidControl, actionDevice} from '@/assets/js/lwm2mMap'
    import handleResponse from '../../restfulapi/handleResponse'
    import selectGroup from "../../../common/select-group"
    import {pluginname} from "../../../assets/js/constant"
    import { number } from 'echarts/lib/export'
    import {appControl} from '@/assets/js/lwm2mMap'
    import {showSystemAppArray, disableAppArray} from "@/assets/js/constant"
    import {singleInstallType, batchInstallType} from "../../../assets/js/constant"
    import {getDeviceStatus as getDeviceStatusAsByTarget, setDeviceStatus as setDeviceStatusByTarget} from '../../restfulapi/deviceStatusApi'
    import {returnDownloadServerUrlApi} from "../../restfulapi/serverConfigApi"

    export default{
        name: 'singleRemoteControl',
        data(){
            return {
                activeName: "systemSetup",
                radio: "1",
                rebootDate: "",
                shutdownDate: "",
                cycle: "",
                audio: 0,
                brightness: 0,
            }
        },

        props: {
            selectedAgentId: {
                type: String,
                default: ""
            },
            isSingleMode: {
                type: Boolean,
                default: true
            },
            //selectedAgent is offline
            isOffline: {
                type: Boolean,
                default: false
            }
        },

        methods:{
            handleBrightness(e){
                return e + '%';
            },
            handleAudio(e){
                return e + '%';
            },
            actionReboot(actions, cid){
                if(!this.selectedAgentId){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                if(!this.rebootDate){
                    this.$swal("",this.$t('global.selectDate'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                if(this.radio==2 && !this.cycle){
                    this.$swal("",this.$t('global.selectCycle'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("remoteControl."+cid) , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                    if (willfunc) {
                        _g.openGlobalLoading();
                        let time = new Date(this.rebootDate);
                        let param={
                            pluginname: pluginname,
                            actions: actions,
                            time: time.getTime(),
                            cycle: this.cycle,
                        }
                        setDeviceStatus(this.selectedAgentId, param).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')})
                                    if(actions.reboot === false){
                                        this.getRebootPlan();
                                    }
                                }else{
                                    _g.handleError(res)
                                }
                            })
                           
                        })

                    }
                })
            },
            actionShutdown(actions, cid){
                if(!this.selectedAgentId){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                if(!this.shutdownDate){
                    this.$swal("",this.$t('global.selectDate'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("remoteControl."+cid) , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                    if (willfunc) {
                        _g.openGlobalLoading();
                        let time = new Date(this.shutdownDate);
                        let param={
                            pluginname: pluginname,
                            actions: actions,
                            time: time.getTime(),
                        }
                        setDeviceStatus(this.selectedAgentId, param).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')})
                                    if(actions.shutdown === false){
                                        this.getShutdownPlan();
                                    }
                                }else{
                                    _g.handleError(res)
                                }
                            })
                           
                        })

                    }
                })
            },

            switchChange(actions, cid){
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')}).then(() => {
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                _g.openGlobalLoading();
                let param={
                    pluginname: pluginname,
                    actions: actions
                }
                setDeviceStatus(this.selectedAgentId, param).then((data) => {
                    handleResponse(data, (res) =>{
                        if(res.status === "CHANGED"){
                            this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')})
                        }else{
                            _g.handleError(res, ()=>{
                                this[cid] = this.$options.data()[cid];
                            })
                        }
                    })
                    
                })
            },

            systemSetupAction(actions, cid){
                if(this.selectedAgentId == ''){
                    this.$swal("",this.$t('global.selectDevice'),"info", {button: this.$t('global.confirm')}).then(() =>{
                        this[cid] = this.$options.data()[cid];
                    })
                    return;
                }
                _g.swalWarnDo(this.$t('global.areYouSure'), this.$t("remoteControl."+cid) , this.$t('global.confirm'), this.$t('global.cancel')).then((willfunc) =>{
                    if (willfunc) {
                        _g.openGlobalLoading();
                        let param={
                            pluginname: pluginname,
                            actions: actions
                        }
                        setDeviceStatus(this.selectedAgentId, param).then((data) => {
                            handleResponse(data, (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("",this.$t('global.success'), "success", {button: this.$t('global.confirm')})
                                }else{
                                    _g.handleError(res)
                                }
                            })
                           
                        })

                    }
                })  
            },

            getShutdownPlan(){
                let param="{'get_shutdown_plan':true}";
                getDeviceStatus(this.selectedAgentId, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let time = parseInt(JSON.parse(res.content.value).shutdownTime);
                            if(time){
                                this.shutdownDate = time;
                            }else{
                                this.shutdownDate = "";
                            }
                        }
                    })
                })   
            },

            getRebootPlan(){
                let param="{'get_reboot_plan':true}";
                getDeviceStatus(this.selectedAgentId, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let time = parseInt(JSON.parse(res.content.value).rebootTime);
                            let cycle = parseInt(JSON.parse(res.content.value).rebootCycle);
                            if(this.radio ==1){
                                if(time && !cycle){
                                    this.rebootDate = time;
                                }else {
                                    this.rebootDate = "";
                                }
                            }else{
                                if(time && cycle){
                                    this.cycle = cycle;
                                    this.rebootDate = time;
                                }else{
                                    this.cycle = "";
                                    this.rebootDate = "";
                                }
                            }
                        }
                    })
                }) 
            },
            getRebootType(){
                let param="{'get_reboot_plan':true}";
                getDeviceStatus(this.selectedAgentId, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            let cycle = parseInt(JSON.parse(res.content.value).rebootCycle); 
                            if(cycle){
                                this.cycle = cycle;
                                this.radio = "2";
                            }else {
                                this.cycle = "";
                                this.radio = "1";
                            }   
                        }
                    })
                }) 
            },

            getDeviceBrightness(){
            let param="{'get_brightness': 0}";
                getDeviceStatus(this.selectedAgentId, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            this.brightness=parseInt(JSON.parse(res.content.value).brightness);
                        }
                    })
                })      
            },

            getDeviceAudio(){
                let param="{'get_audio': true}";
                getDeviceStatus(this.selectedAgentId, param).then((data) => {
                    handleResponse(data, (res) => {
                        if(res.status === "CONTENT"){
                            this.audio=parseInt(JSON.parse(res.content.value).get_audio);
                        }
                    })
                })      
            },

            handleClick(tab){
                if(tab.name === "systemSetup"){
                    this.$emit("isDisabled", false)
                }else{
                    this.$emit("isDisabled", true)
                }
            },

            initRebootData(){
                this.rebootDate="";
                this.cycle="";
            },
            initShutdownData(){
                this.shutdownDate="";
            },
            initData(){
                this.audio = 0;
                this.brightness = 0;
            },
        },
        watch: {
            isSingleMode(val){
                if(val){
                    this.activeName = "systemSetup";
                    this.initShutdownData();
                    this.initRebootData();
                    this.initData();
                    if(this.selectedAgentId){
                        this.getShutdownPlan();
                        this.getRebootPlan();
                        this.getRebootType();
                        this.getDeviceBrightness();
                        this.getDeviceAudio();
                    }
                }
            },

            selectedAgentId:function(){
                this.initShutdownData();
                this.initRebootData();
                this.initData();
                if(this.selectedAgentId){
                    this.getShutdownPlan();
                    this.getRebootPlan();
                    this.getRebootType();
                    this.getDeviceBrightness();
                    this.getDeviceAudio();
                }
            },
            radio:function(){
                this.initRebootData();
                if(this.selectedAgentId){
                    this.getRebootPlan();
                }
            }
        },
        computed: {
            options(){
                return [{
                    value: 1,
                    label: this.$t('remoteControl.oneDay')
                }, {
                    value: 7,
                    label: this.$t('remoteControl.oneWeek')
                }, {
                    value: 30,
                    label: this.$t('remoteControl.oneMonth')
                }, {
                    value: 90,
                    label: this.$t('remoteControl.threeMonths')
                }, {
                    value: 180,
                    label: this.$t('remoteControl.sixMonths')
                }, {
                    value: 365,
                    label: this.$t('remoteControl.oneYear')
                }]
            } 
        }     
    }
</script>

<style lang='scss' scoped>
@import "@/assets/css/colors.scss";
.pd-5{
    padding:5px
}
.counter{
    display: inline-block;
    width: 150px;
}
.info-box {
    display: block;
    min-height: 180px;
    background: #fff;
    width: 100%;
    box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
    border-radius: 3px;
    margin: 15px 0px 15px 0px;;
    text-align: center;
  
}
.info-box-icon {
    border-radius: 50%;
    display: block;
    /* float: left; */.p{
        margin-top: 20px; 
    }
    .i{
        margin-top: 20px;
        padding-right: 30px;
        width: 150; 
    }
    .span{
        display: block;
        float: left;
    }
    height: 120px;
    width: 120px;
    
    line-height: 40px;
    font-size: 45px;
    /* background: rgba(0, 0, 0, 0.2); */
    margin-top: 15px;
    margin:1rem auto;
    i{
        margin-top: 20px;
    }
}
.info-box-icon-white {
  color: #fff;
}
.info-box-icon-title{
	color:#fff;
    font-size:15px;
    margin-bottom: 20px;
}
.date{
    min-height: 130px;
}
</style>



