<template>
    <div>
        <el-table
            :data="pluginsStatusHistoryTableList"
            tooltip-effect="dark"
            style="width: 100%"
            @selection-change="selectItem"
            @filter-change="filterStatus"
            :default-sort = "{prop: 'ts', order: 'descending'}"
            ref="pluginsStatusHistoryTable"
            v-loading="listLoading" 

            >
            <el-table-column
            type="selection"
            min-width="50"
            :selectable="checkSelectable">
            </el-table-column>

            <el-table-column
            prop="devname"
            :label="$t('remoteControl.deviceName')"
            min-width="120"
            >
            </el-table-column>

            <el-table-column
            prop="taskname"
            :label="$t('remoteControl.taskName')"
            min-width="120"
            v-if="statusType == batchInstallType"
            >
            </el-table-column>

            <el-table-column
            :label="$t('remoteControl.beginTime')"
            min-width="130"
            prop="ts"
            >
                <template slot-scope="scope">
                    {{scope.row.ts|time}}
                </template>
            </el-table-column>

            <el-table-column
            prop="function"
            :label="$t('remoteControl.function')"
            min-width="100"
            >
            </el-table-column>

            <el-table-column
            prop="cycle"
            :label="$t('remoteControl.cycleTime')"
            min-width="100"
            >
                <template slot-scope="scope">
                    {{scope.row.cycle|none}}
                </template>
            </el-table-column>
            
            <el-table-column
            :label="$t('remoteControl.dateTime')"
            min-width="130"
            prop="datetime"
                >
                <template slot-scope="scope">
                    {{scope.row.datetime|time}}
                </template>
            </el-table-column>

            <el-table-column
            column-key="status"
            prop="status"
            :label="$t('remoteControl.status')"
            min-width="130"
            :filters="[{ text: $t('global.success'), value: 2 }, { text: $t('global.failed'), value: 1 }, {text: $t('global.running'), value: 0}]"
            filter-placement="bottom-end"
            >
                <template slot-scope="scope">
                    <img src="@/assets/imgs/loading1.gif" alt="" style="width:50px;height:10px;" v-if="scope.row.status==0">
                    <span v-if="scope.row.status==2" class="badge bg-green">{{$t('global.success')}}</span>
                    <span v-if="scope.row.status==1" class="badge bg-DimGray">{{$t('global.failed')}}</span>
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('remoteControl.errorStatus')"
            min-width="100"
            >
                <template slot-scope="scope">
                    {{(""+scope.row.errorcode)|appErrorStatus}}
                </template>
            </el-table-column>

            <el-table-column
            :label="$t('appControl.action')"
            min-width="175"
            align="center">
                <template slot-scope="scope">
                    <div>
                        <el-button-group>
                            <el-button 
                                size="mini" 
                                type="danger"
                                @click="deleteHistory(scope.row.psid)"
                                :disabled="scope.row.status==0"
                                >
                                    {{$t('global.delete')}}
                            </el-button>
                            <el-button 
                                size="mini" 
                                type="primary"
                                @click="reSetup(scope.row)"
                                :disabled="scope.row.status!=1"
                                >
                                    {{$t('appControl.reinstall')}}
                            </el-button>
                        </el-button-group>
                    </div>
                </template>
            </el-table-column>

        </el-table>

        <btn-group 
        :selectedData="multipleSelection" 
        :isDelete="true" 
        :isMove="false" 
        tid="psid" 
        :delApi="batchDeletePluginsStatusHistory" 
        @result="isSuccess" 
        v-show="isShowBtnGroup"
        class="fl m-t-10 cf">
        </btn-group>

        <el-button 
        v-loading="clearLoading" 
        size="small" 
        @click="clearAll()" 
        type="primary" 
        class="fl m-t-10 cf m-l-10"
        v-show="isShowBtnGroup">
        {{$t('appControl.clearAll')}}
        </el-button>

        <div class="m-t-10 cf fr">
            <el-pagination
            @current-change="handleCurrentChange"
            layout="prev, pager, next"
            :page-size="limit"
            :current-page="currentPage"
            :total="dataCount"
            v-show="isshow"
            background
            >
            </el-pagination>
            
        </div> 
        
       
    </div>
</template>
<script>
    import {appControl} from '@/assets/js/lwm2mMap'
    import {reExecDeviceStatus} from '@/components/restfulapi/deviceStatusApi';
    import{
        getPluginsStatusByPageInGroupApi,
        getPluginsStatusByPageAndStatusInGroupApi,
        batchDeletePluginsStatusHistoryApi,
        deletePluginsStatusHistoryAllByGroupApi,
        deletePluginsStatusHistoryApi,
        repeatSetDeviceStatusByPlugins
    } from "@/components/restfulapi/pluginsApi"
    import {pluginname, systemSetupBatch} from "../../../assets/js/constant"
    import {setLocal, getLocal, getSession, checkSession, setSession} from "../../../assets/js/storage"
    import handleResponse from '@/components/restfulapi/handleResponse'
    import btnGroup from "../../../common/btn-group";
    import {singleInstallType, batchInstallType} from "../../../assets/js/constant"
    import pluginsHistory from "./history"

export default {
    name: 'batchSetupHistory',
    data() {
        let validateTaskname = (rule,value,callback)=>{
            let reg=new RegExp(/^(?![._-])[a-zA-Z0-9\.\-_]{3,12}$/);
            if(!reg.test(value)){
                return callback(new Error (this.$t('global.verifyShortError')))
            }else{callback()};
        };
        return {
            setupStatusForm:{
                taskname: '',
            },
            rules:{
                taskname: {validator: validateTaskname, trigger: 'blur'}
            },

            pluginsStatusHistoryTableList: [],
            isshow: false,
            limit: 10,
            dataCount: 0,
            currentPage: 1,
            multipleSelection: [],
            isShowBtnGroup: false,
            clearLoading: false,
            batchDeletePluginsStatusHistory:  batchDeletePluginsStatusHistoryApi,
            intervalTime: 7000,

            isInterval: false,
            listLoading: false,
            singleInstallType,
            batchInstallType,
            statusArray: [],
        }
    },

    components: {
         btnGroup,
    },

    props: {
        statusType: {
            default: batchInstallType,
            type: String
        },

        selectedAgentId: {
            default: "",
            type: String
        },

        groupname: {
            default: "",
            type: String
        }
    },
    methods: {

        isSuccess(result){
            if(result){
                this.intervalGetPluginsStatusHistory();
            }
        },

        reSetup(pluginsItem){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('appControl.reinstall'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    let param;
                    if(pluginsItem.function.indexOf('Schedule') != -1){
                        param={
                            pluginname: pluginname,
                            actions: pluginsItem.actions,
                            cycle: pluginsItem.cycle,
                            time: pluginsItem.datetime,
                            taskname: pluginsItem.taskname
                        }
                    }else{
                        param={
                            pluginname: pluginname,
                            actions: pluginsItem.actions,
                            taskname: pluginsItem.taskname
                        }
                    }      
                            
                    repeatSetDeviceStatusByPlugins(pluginsItem.endpoint, param).then((data) => {
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                               this.intervalGetPluginsStatusHistory();
                            }else{
                                this.intervalGetPluginsStatusHistory();
                            }
                        })
                        
                    })
                }
            })
        },

        deleteHistory(psid){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.delete'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.listLoading = true;
                    deletePluginsStatusHistoryApi(psid).then((data) => {
                        this.listLoading = false;
                        handleResponse(data, (res) => {
                            if(res.status === "CHANGED"){
                                this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                        this.intervalGetPluginsStatusHistory();
                                })
                            }else{
                                _g.handleError(res);
                                this.intervalGetPluginsStatusHistory();
                            }
                        })
                    })
                }
            }) 
        },
        clearAll(){
            _g.swalInfoDo(this.$t('global.areYouSure'), this.$t('global.clearAll'), this.$t('global.confirm'), this.$t('global.cancel')).then((result) => {
                if(result){
                    this.clearLoading = true;
                    if(this.statusType == batchInstallType){
                        deletePluginsStatusHistoryAllByGroupApi(this.groupname,systemSetupBatch).then((data) => {
                            this.clearLoading = false;
                            handleResponse((data), (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.intervalGetPluginsStatusHistory();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }else{
                        deletePluginsStatusHistoryAllByDevIdApi(this.selectedAgentId).then((data) => {
                            this.clearLoading = false;
                            handleResponse((data), (res) => {
                                if(res.status === "CHANGED"){
                                    this.$swal("", this.$t('global.success'), "success", {button: this.$t('global.confirm')}).then(() => {
                                            this.intervalGetPluginsStatusHistory();
                                    })
                                }else{
                                    _g.handleError(res);
                                }
                            })
                        })
                    }  
                }
            })

        },
        checkSelectable(row) {
            return !row.status==0;
        },
        selectItem(val) {
            this.multipleSelection = val;
        },
        handleCurrentChange(currentPage){
            if(currentPage){
                this.currentPage = currentPage;
            }else{
                this.currentPage = 1
            }
            this.intervalGetPluginsStatusHistory();
        },
        filterStatus(value){
            this.statusArray = value.status;
            this.currentPage = 1;
            if(_g.isArray(this.statusArray)){
                this.intervalGetPluginsStatusHistory();
            }
        },
        assignPluginsStatusData(res){
            if(Object.prototype.toString.call(res)  === '[object Object]'&& Object.prototype.toString.call(res.data) === '[object Array]'){
                this.dataCount = res.count;
                this.pluginsStatusHistoryTableList = res.data
                this.isshow = this.dataCount > this.limit;
                this.isShowBtnGroup = this.dataCount > 0;
                let isFinish = true;
                if(this.statusType == batchInstallType){
                    this.$emit("getStatusCount")
                }
                res.data.forEach((val) => {
                    if(val.status == 0){
                        isFinish = false;
                    }
                })
                if(isFinish){
                    
                    this.$emit("isFinished");
                    
                    window.clearInterval(_g.timer);
                    _g.timer = null;
                }
            }else{
                window.clearInterval(_g.timer);
                _g.timer = null;
            }
        },
        getPluginsStatusHistoryByGroupname(){
            if(!this.groupname){
                console.error("[getPluginsStatusHistoryByGroupname]groupname is empty")
            }
            if(this.isInterval === false){
                this.listLoading = true;
            }
            
            if(this.statusArray.length > 0){
                getPluginsStatusByPageAndStatusInGroupApi(this.groupname,this.currentPage, this.limit, this.statusArray, systemSetupBatch).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignPluginsStatusData(res);
                    })
                })
            }else{
                getPluginsStatusByPageInGroupApi(this.groupname, this.currentPage, this.limit, systemSetupBatch).then((data) => {
                    this.listLoading = false;
                    handleResponse(data, (res) => {
                        this.assignPluginsStatusData(res);
                    })
                })
            }
        },

        intervalGetPluginsStatusHistory(){
            if(this.statusType == batchInstallType){
                this.getPluginsStatusHistoryByGroupname();
                if(_g.timer === null){
                    _g.timer = window.setInterval(() => {
                        this.getPluginsStatusHistoryByGroupname();
                        this.isInterval = true;
                    },this.intervalTime)
                } 
            }else{
                this.getPluginsStatusHistoryBydevid();
                if(_g.timer === null){
                    _g.timer = window.setInterval(() => {
                        this.getPluginsStatusHistoryBydevid();
                        this.isInterval = true;
                    },this.intervalTime)
                }
            }
        },


        initData(){
            this.isshow= false;
            this.isShowBtnGroup= false;
            this.pluginsStatusHistoryTableList=[];
            this.currentPage = 1;
        }


    },

    created(){
        if(this.statusType == batchInstallType){
            this.$emit("getStatusCount")
        }
    },
    beforeDestroy() {
        
    },

    watch: {
        groupname(val){
            this.initData();
            if(val){
                this.intervalGetPluginsStatusHistory();
                
            }  
　　　　　},
        selectedAgentId(val){
            this.initData();
            if(val){
                this.intervalGetPluginsStatusHistory();
                
            }  
　　　　　},

    },
        
}
</script>
<style lang="scss" scoped>
@import "@/assets/css/colors.scss";
    .badge{
        display: inline-block;
        min-width: 10px;
        padding: 5px 8px;
        font-size: 12px;
        font-weight: 700;
        line-height: 1;
        color: #fff;
        text-align: center;
        white-space: nowrap;
        vertical-align: middle;
        // background-color: #777;
        border-radius: 10px;
    }

    .el-loading-mask{
        top: 60px !important;
    }


    
</style>



